<template>
  <v-container id="precisetrainpage" fluid tag="section">
    <remote-js src="https://cdn.bootcss.com/mathjax/2.7.0/MathJax.js?config=TeX-AMS-MML_HTMLorMML"></remote-js>
    <template v-if="flag6 == 0" style="margin: 0 auto;">
      <v-card height="510px" class="mx-auto">
        <div style="margin: 10px 10px;">
          <v-card-title class="`text-$subtitle-1`">
            <div style="margin-top: 25px;">{{ name }}</div>
            <v-spacer></v-spacer>
          </v-card-title>
          <v-card-text style="margin-top: 30px; font-size: 16px;">
            题量：{{ len }} 道
          </v-card-text>
          <v-card-text style="font-size: 16px;">
            时长：{{ duration }} 分钟
          </v-card-text>
        </div>
        <v-card-actions>
          <div style="display: flex; justify-content: center; align-items: center;">
            <v-btn @click="start" height="40px" color="orange accent-3" style="font-size: 16px; font-weight:bold;">开始答题</v-btn>
          </div>
        </v-card-actions>
      </v-card>
      
    </template>

    <template v-else>
      <v-row>
        <v-col cols="12" md="3">
          <v-card height="490px">
            <circular-count-down-timer
              :initial-value="$global.duration * 60"
              :stroke-width="5"
              :size="130"
              :padding="10"
              :hour-label="'时'"
              :minute-label="'分'"
              :second-label="'秒'"
              :show-second="true"
              :show-minute="true"
              :show-hour="true"
              @finish="finished"
            ></circular-count-down-timer>
          </v-card>
        </v-col>
        <v-col cols="12" md="9">
          <v-card id="this_p" height="490px">
            <div style="margin: 15px 15px;">
              <v-card-title>
                <div v-html="des" style="margin-top: 10px;"></div>
              </v-card-title>
              <div v-if="paper[p-1].qc == 'choice'">
                <v-card-text class="text--primary">
                  <div v-for="n in 4" :key="n-1" v-html="cho[n-1]" style="margin-top: 20px; font-size: 18px;"></div>
                </v-card-text>
                <v-card-actions>
                  <v-radio-group id="this_ans" v-model="ans[p-1]" :mandatory="false" row>
                    <v-radio v-for="n in 4" :key="n-1" :label="choices[n-1]" :value="n-1" style="font-size: 18px; margin-right: 30px;"></v-radio>
                  </v-radio-group>
                </v-card-actions>
              </div>
              <div style="display: flex; justify-content: center; align-items: center;">
                <v-btn v-if="p == len" @click="submit(0)" height="40px" color="orange accent-3" style="font-size: 18px; font-weight:bold;">交卷</v-btn>
              </div>
            </div>
          </v-card>
        </v-col>
      </v-row>
      <v-row style="margin-top: 10px; display: flex; justify-content: center; align-items: center;">
        <v-pagination v-model="p" class="my-4" :length="$global.len"></v-pagination>
        {{picker}}
      </v-row>
    </template>
  </v-container>

</template>

<script>
  export default {
    name: 'precisetrainpage',
    components: {
      'remote-js': {
        render(createElement) {
          return createElement('script', {
            attrs: {
              type: 'text/javascript',
              src: this.src,
              onload: this.onload
            }
          });
        },
        props: {
          src: {
            type: String,
            required: true
          },
        },
      },
    },
    data() {
      return {
        paper: [],
        len: 0,
        flag6: 0,
        p: 1,
        ans: [],
        ans_checked: [],
        des: '',
        cho: [],
        choices: ["A", "B", "C", "D"],
        picker: '',
        duration: '',

      }
    },
    created: function () {
      this.$global2.load_config()
      this.$global2.load_css()
      this.paper = this.$global.paper
      this.len = this.$global.len
      this.duration = this.$global.duration
      setTimeout(() => {
        this.des = this.p + ". " + this.$global.paper[0].des
        this.cho = this.$global.paper[0].cho
      }, 1000)
      console.log(this.paper)
      console.log(this.len)
    },

    // computed: {
    //   this_paper_des: function () {
    //     return this.paper[this.p - 1].des
    //   },
    //   this_ans: function () {
    //     return this.ans[this.p - 1]
    //   }
    // },

    methods: {
      start: function () {
        var that = this
        that.des = that.p + ". " + that.paper[0].des
        that.cho = that.paper[0].cho
        var d = new Date();
        that.start_time = d.getTime()
        setTimeout(() => {
          that.flag6 = 1;
          that.$nextTick(function () {
            that.$global2.MathQueue("this_p");
          })
        }, 300)
      },
      submit: function () {
        var that = this;
        let flag = true;
        for (var i = 0; i < that.len; i++) {
          if (that.paper[i].qc == 'choice' && (that.ans[i] == null || that.ans[i].length == 0)) {
            flag = false
            break
          }
          if (that.ans[i] == that.paper[i].ans.charCodeAt(0) - 65) {
            that.ans_checked[i] = "0"
          } else if (that.paper[i].qc != 'choice') {
            that.ans_checked[i] = "-1"
          } else {
            that.ans_checked[i] = "1"
          }
        }
        console.log(that.ans_checked)
        if (flag) {
          that.$axios({
            method: 'get',
            url: '/api/get_train_done/',
            params: {
              stu_id: that.$global.userMsg.id,
              start_time: that.start_time,
              q_num: that.len,
              q_id: JSON.stringify(that.$global.q_id),
              wrong: JSON.stringify(that.ans_checked),
              name: that.$global.paper_name
            }
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              alert("交卷成功！")
            } else if (response.data.error_num == 1) {
              alert("交卷失败！")
            }
          })
          that.$router.push({
            path: './statistics'
          })
        } else {
          alert("有题目未作答！")
        }
      },
      finished: () => {
        console.log('finished');
      },
    },

    watch: {
      p: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("p = ", oldVal, newVal)
          if (that.flag6 == 1) {
            // that.refresh = false
            that.des = that.p + ". " + that.paper[newVal - 1].des
            that.cho = that.paper[newVal - 1].cho
            that.$nextTick(function () {
              that.$global2.MathQueue("this_p");
            })
          }
        },
        deep: true,
        immediate: true
      },
    },
  }
</script>
